import React from 'react'
// css in app.css =p
import Navbar from '../Components/Navbar';
import Footer from '../Components/Footer';
const TermsContainer = () => {
    return (
        <>
            <Navbar />
            <div className="content-container">
            <div className="guide-field">
            <div className="container">
        <h1 className="fs-md-32 text-heading lh-141 mb-6 mxw-751 text-center">
          ĐIỀU KHOẢN VÀ QUY ĐỊNH CHUNG  THUÊ NHÀ</h1>
        <div className="mxw-751">
          <div className="lh-214 mb-9">
            <p style={{textAlign: 'left'}} align="center"><span ><strong>1.&nbsp;</strong><strong>TRẠNG THÁI BAN ĐẦU &amp; KẾT THÚC CHO THUÊ</strong></span></p>
            <p>Vào thời điểm bắt đầu của Hợp đồng cho thuê, Căn hộ sẽ được giao cho và được chấp nhận bởi Bên thuê trong tình trạng hiện tại của Căn hộ.</p>
            <p>Bên thuê phải bàn giao Căn hộ cho Bên cho thuê vào thời điểm kết thúc Hợp đồng trong tình trạng như lúc bắt đầu thời điểm thuê nhà, ngoại trừ những sửa chữa được tiến hành bởi Bên cho thuê và những xuống cấp thông thường trong quá trình thuê nhà.</p>
            <p><span >&nbsp;<strong>2.&nbsp;</strong><strong>SỬ DỤNG NHÀ CHO THUÊ</strong>&nbsp;</span></p>
            <p><strong>2.1</strong>&nbsp;Căn hộ này sẽ được Bên Thuê sử dụng chỉ để làm nơi ở cho cá nhân hoặc hộ gia đình. Bên Thuê không được sử dụng toàn bộ hoặc bất lì phần nào của Căn hộ này vào bất kỳ thời điểm nào trong thời hạn của Hợp đồng thuê nhà này cho mục đích tiến hành bất kỳ hoạt động kinh doanh hoặc nghề nghiệp thuộc bất kỳ loại hình nào, hoặc bất cứ hoạt động bất hợp pháp nào đối với Luật Việt Nam, hoặc cho bất kỳ mục đích nào mà không phải là làm nơi ở cho cá nhân hoặc hộ gia đình.&nbsp;</p>
            <p><strong>2.2</strong>&nbsp;Bất cứ việc sửa chữa hoặc cải tạo nào nhằm mục đích sử dụng riêng của Bên Thuê phải được chấp thuận bằng văn bản của Bên Cho Thuê và phải tuân theo những qui định về xây dựng của Luật pháp Việt Nam.&nbsp;</p>
            <p><strong>2.3</strong>&nbsp;Bên Thuê phải tuân thủ mọi quy định pháp luật về vệ sinh, y tế, phòng cháy chữa cháy, vệ sinh môi trường, trật tự an ninh của các cơ quan Nhà nước có thẩm quyền và hiệp hội chủ nhà/chủ chung cư, nếu có, liên quan đến Căn hộ.</p>
            <p><span >&nbsp;<strong>3.&nbsp;</strong><strong>SỐ NGƯỜI CƯ NGỤ</strong><strong>&nbsp;</strong></span></p>
            <p><strong>3.1.</strong> Bên Thuê đồng ý rằng số người cư ngự thường xuyên tại Căn hộ sẽ không vượt quá số người được quy định như thỏa thuận trong Hợp đồng cho thuê mà không có sự chấp thuận trước bằng văn bản của Bên Cho Thuê.&nbsp;</p>
            <p><strong>3.2.</strong> Cung cấp bản phô tô Hộ Chiếu và Thị Thực (của các thành viên trong gia đình) cho Bên Cho Thuê để thực hiện việc đăng ký tạm trú vào ngày khách chuyển vào căn hộ. Nếu Bên Thuê không cung cấp để đăng ký với Công An khu vực thì Bên Thuê sẽ phải chịu trách nhiệm trước Pháp Luật Việt Nam.&nbsp;</p>
            <p><strong>3.3.</strong> Trong trường hợp có khách đến thăm Bên Thuê tại Căn hộ và tạm trú lại, Bên Thuê phải thông báo cho Bên Cho Thuê về danh tính và thời gian lưu trú, cung cấp visa, hộ chiếu hoặc giấy tờ cá nhân của khách cho Bên Cho Thuê. Bên Cho Thuê không được từ chối chấp thuận một cách bất hợp lý về việc này.&nbsp;</p>
            <p><strong>3.4.</strong> Bên Cho Thuê có trách nhiệm xin cấp và/hoặc duy trì Giấy phép cho người nước ngoài thuê nhà, theo qui định của luật pháp Việt Nam, đối với thời hạn thuê nhà và gia hạn thuê nhà theo hợp đồng thuê nhà. Có trách nhiệm thực hiện việc đăng ký tạm trú cho Bên Thuê theo đúng quy định và cung cấp cho Bên Thuê những chứng nhận liên quan.</p>
            <p><span  >&nbsp;<strong>4.</strong>&nbsp;<strong>GIA HẠN</strong></span></p>
            <p><strong>4.1.</strong> Bên Thuê có quyền lựa chọn gia hạn Thời hạn của Hợp đồng thuê nhà này bằng cách thông báo cho Bên Cho Thuê ý định gia hạn của mình ít nhất 30 (ba mươi) ngày trước Ngày hết hạn.</p>
            <p><strong>4.2.</strong> Nếu Bên thuê không gia hạn hợp đồng đúng thời hạn, Hợp đồng thuê nhà và mọi dịch vụ liên quan đến căn hộ sẽ bị dừng. (hệ thống điện, thẻ từ...)&nbsp; &nbsp; &nbsp; &nbsp;&nbsp;</p>
            <p><span  ><strong>&nbsp;5.&nbsp;</strong><strong>DỊCH VỤ TRONG CĂN HỘ</strong>&nbsp;</span></p>
            <p><strong>5.1.</strong> Vấn đề về internet, bên thuê được sử dụng internet miễn phí do Bên Cho Thuê cung cấp&nbsp; với tốc độ tối đa cam kết là: 12 MB/s</p>
            <p><strong>5.2.</strong> Dịch vụ truyền hình cáp cũng được cung cấp cho bên thuê với 65 kênh phát sóng, chi tiết có thể tham khảo tại đường link: <a href="http://www.vtvcab.vn/dich-vu/truyen-hinh-cap/vtvcab-tai-ha-noi/bang-kenh-analog">http://www.vtvcab.vn/dich-vu/truyen-hinh-cap/vtvcab-tai-ha-noi/bang-kenh-analog</a>&nbsp;</p>
            <p><strong>5.3.</strong> Dịch vụ lau dọn, giặt là (3 lần/tuần) và phí sử dụng nước sinh hoạt chỉ áp dụng cho 2 người ở. Nếu có phát sinh thêm người thứ 3 với thời hạn ở Căn hộ là 01 tháng trở lên thì sẽ tính phí phụ thu như thông tin chi tiết được ghi dưới đây:</p>
            <ul>
              <li>Dịch vụ lau dọn và giặt là: $50/người/tháng (Tương đương 1,00,000 VND)</li>
              <li>Nước sinh hoạt: $10/người/tháng (Tương đương 200,000 VND)</li>
            </ul>
            <p><strong><em>&nbsp;</em>5.4.</strong> Mỗi căn hộ sẽ được cấp tối đa 2 bộ chìa khóa. Trong trường hợp Bên Thuê làm mất hay cần cấp thêm thì sẽ phải trả thêm chi phí phụ thu: 100,000 VND/bộ&nbsp;</p>
            <p><strong>5.5.</strong> Tiền điện áp dụng cho những căn hộ dùng chung máy giặt, máy sấy sẽ là VND 200,000/tháng/căn hộ.&nbsp;</p>
            <p><strong>5.6.</strong> Phục vụ ăn sáng tại căn hộ (Nếu có)&nbsp;</p>
            <p><strong>5.7.</strong> Đưa đón ra sân bay Quốc tế Nội Bài (Nếu có)&nbsp;</p>
            <p><strong>5.8.</strong> Truyền hình cáp quốc tế và truyền hình Nhật NHK (Theo thỏa thuận)&nbsp;</p>
            <p><strong>5.9.</strong> Bảo trì và bảo dưỡng định kì (Chi tiết đính kèm theo danh sách các Quy định)</p>
            <p><span  ><em>&nbsp;</em><strong>6.</strong><strong>&nbsp;PHƯƠNG THỨC THANH TOÁN</strong><strong>&nbsp;</strong></span></p>
            <p>Bên Thuê có nghĩa vụ trả tiền thuê, hóa đơn điện và các chi phí khác liên quan đúng hạn như quy định trong Hợp đồng cho thuê.&nbsp;</p>
            <p><span  ><strong>&nbsp;7.&nbsp;</strong><strong>CHUYỂN NHƯỢNG VÀ CHO THUÊ LẠI</strong>&nbsp;</span></p>
            <p><strong>7.1.</strong> Bên Cho Thuê không được chuyển nhượng hợp đồng thuê này hoặc không được cho thuê lại Căn Hộ cho những người khác vì bất cứ lí do gì trong thời hạn Hợp đồng thuê nhà này còn hiệu lực.&nbsp;</p>
            <p><strong>7.2.</strong> Bất kỳ việc chuyển nhượng hoặc cho thuê lại mà không có sự đồng ý trước bằng văn bản của Bên Cho Thuê sẽ bị vô hiệu hóa và cấu thành hành vi vi phạm nghĩa vụ của Bên Thuê theo Điều này.</p>
            <p>&nbsp;<span  ><strong>8.&nbsp;</strong><strong>THAY ĐỔI, BẢO TRÌ VÀ SỬA CHỮA</strong>&nbsp;</span></p>
            <p><strong>8.1.</strong> Bên Thuê không được thay đổi Căn hộ hoặc xây dựng bất kỳ kết cấu hoặc thực hiện những sửa sang khác mà không có sự đồng ý trước bằng văn bản của Bên Cho Thuê sẽ bị cho là vi phạm nghĩa vụ của Bên Thuê theo điều này.&nbsp;</p>
            <p><strong>8.2.</strong> Tất cả các sửa chữa được thực hiện trong vòng 1 tuần kể từ ngày nhận được yêu cầu từ Bên Thuê. Nếu không Bên Thuê sẽ có quyền sửa chữa những hư hỏng này mà không thông báo thêm cho Bên Cho Thuê. Nếu Bên Thuê sửa chữa những hư hỏng này thì Bên Thuê sẽ yêu cầu Bên Cho Thuê thanh toán chi phí sửa chữa hoặc tự động khấu trừ vào Tiền thuê phải trả<em><br /></em></p>
            <p><strong>8.3.</strong> Với những căn hộ không có người lau dọn, để đảm bảo an toàn cho người ở, chủ nhà sẽ có kế hoạch vào kiểm tra và bảo dưỡng các thiết bị điện của căn hộ. Lịch kiểm tra sẽ được thông báo trước cho khách hàng.&nbsp;</p>
            <p><span  ><strong>&nbsp;9.&nbsp;</strong><strong>THÚ NUÔI</strong><strong>&nbsp;</strong></span></p>
            <p>Trong trường hợp khách có nuôi chó, mèo trong căn hộ, Bên Thuê sẽ phải tuân thủ những quy định như sau:</p>
            <p><strong>9.1. </strong>Phải cung cấp đầy đủ giấy chứng nhận về tiêm chủng chó mèo&nbsp;</p>
            <p><strong>9.2.</strong> Trong trường hợp chó, mèo không còn ở trong căn hộ nữa, bên thuê phải dọn dẹp sạch sẽ&nbsp; khu vực ngoài căn hộ và cả những khu vực liên quan phía trong căn hộ.&nbsp;</p>
            <p><strong>9.3.</strong> Không được để chó, mèo đi ra ngoài mà không có sự giám sát hoặc bảo vệ đúng cách. Để giảm thiểu nguy hiểm có thể gây ra cho những người thuê nhà khác, phải đeo bảo vệ cho chó, mèo khi chúng đi ra ngoài.&nbsp;</p>
            <p><strong>9.4.</strong> Bên thuê sẽ phải chịu trách nhiệm đền bù nếu có bất cứ hư hại gì về thiết bị hay căn hộ mà gây ra bởi chó mèo&nbsp;</p>
            <p><strong>&nbsp;</strong><strong>10.&nbsp;</strong><strong>QUYỀN VÀO NHÀ CHO THUÊ</strong>&nbsp;</p>
            <p>Bên Cho Thuê và/hoặc đại diện của Bên Cho Thuê chỉ có quyền vào Căn hộ sau khi có sự cho phép từ Bên thuê về thời gian,ngày tháng, cho mục đích:&nbsp;</p>
            <p>&nbsp; &nbsp; &nbsp; &nbsp;<strong> &nbsp;i.</strong> Kiểm tra Căn hộ và/hoặc thực hiện việc sửa chữa thay thế đối với Căn hộ hoặc các hạng mục trong Căn hộ như quy định theo Hợp đồng thuê nhà này.&nbsp;</p>
            <p>&nbsp; &nbsp;<strong> &nbsp; &nbsp; &nbsp;ii.</strong> đưa người thuê nhà khác xem Căn hộ trong thời gian 30 (ba mươi) ngày cuối cùng của Hợp đồng thuê nhà này; hoặc&nbsp;</p>
            <p>&nbsp; &nbsp;<strong> &nbsp; &nbsp; &nbsp;iii.</strong> khắc phục tình trạng khẩn cấp.</p>
            <p><span  >&nbsp;<strong>11.&nbsp;</strong><strong>AN NINH VÀ BẢO VỆ TÀI SẢN</strong></span></p>
            <p><strong>&nbsp;11.1. </strong>Liên quan đến vấn đề an ninh: Bên Cho Thuê không chịu trách nhiệm theo sát trong việc đảm bảo an toàn cho Bên Thuê. Bên Thuê sẽ phải chịu trách nhiệm về sự an toàn của tài sản thuộc quyền sở hữu của họ. Tất cả cửa sổ và cửa chính nên được đóng, khóa cẩn thận. Bất cứ ai ghé thăm phải được kiểm tra cẩn thận trước khi cho vào căn hộ. Cố gắng hạn chế không đi ra ngoài công viên, bãi đỗ xe một mình, đặc biệt sau khi trời tối. Nếu có bất cứ sự việc đáng nghi ngờ nào xung quanh căn hộ, hãy gọi cho Bên Cho Thuê hoặc bảo vệ tòa nhà ngay lập tức.&nbsp;</p>
            <p><strong>11.2.</strong> Việc bảo vệ tài sản của Bên Thuê (như là: két sắt, tiền mặt, trang sức…) hoàn toàn là trách nhiệm của Bên Thuê.</p>
            <p><strong>11.3.</strong> Bên cho thuê sẽ không chịu trách nhiệm với bất cứ hỏng hóc, mất mát gì về đồ đạc cá nhân của Bên thuê hoặc các thiết bị khác trong khu vực cho thuê mà gây ra bởi Bên thuê hoặc bên thứ 3.</p>
            <p><strong>11.4.</strong> Mọi hành vi tổ chức đánh bạc, mại dâm đều bị nghiêm cấm theo pháp luật Việt Nam.&nbsp;</p>
            <p><span  ><strong>&nbsp;12.&nbsp;</strong><strong>CHẤM DỨT HỢP ĐỒNG</strong></span></p>
            <p>&nbsp;Hợp đồng thuê này được chấm dứt trong những trường hợp sau:</p>
            <p><strong>12.1.</strong> Trong trường hợp Bên Thuê bị trục xuất hoặc bị thu hồi giấy phép hoạt động tại Việt Nam hoặc người cư ngụ của căn nhà thuyên chuyển công tác ra nước ngoài. Bên thuê phải thông báo trước cho Bên Cho Thuê 30 ngày bằng văn bản. Bên Cho Thuê sẽ hoàn trả lại Bên Thuê đầy đủ toàn bộ số tiền đặt cọc và khoản tiền thuê nhà đã trả trước còn lại không sử dụng.<em>&nbsp;</em></p>
            <p><strong>12.2.</strong> Trong trường hợp nhà cho thuê bị tháo dỡ theo quyết định của các cấp có thẩm quyền hoặc bị cháy (không do lỗi của Bên Thuê) hoặc giấy phép thuê nhà cho người nước ngoài không thể gia hạn. Bên Cho Thuê sẽ hoàn trả lại Bên Thuê đầy đủ toàn bộ số tiền đặt cọc và khoản tiền thuê nhà đã trả trước còn lại không sử dụng.<em>&nbsp;</em></p>
            <p><strong>12.3.</strong> Trường hợp Bất Khả Kháng: Thiên tai, lũ lụt, chiến tranh, tiếng ồn từ công trường xây dựng hoặc môi trường xung quanh, Bên Cho Thuê sẽ hoàn trả lại Bên Thuê đầy đủ toàn bộ số tiền đặt cọc và khoản tiền thuê nhà đã trả trước còn lại không sử dụng từ Bên Thuê. Trong trường hợp này, Bên Thuê có thể đơn phương chấm dứt hợp đồng mà không bị phạt, tuy nhiên phải gửi cho Bên Cho Thuê thông báo trước 01 tháng.&nbsp;</p>
            <p><strong>12.4.</strong> Trong trường hợp tiếng ồn từ công trình xây dựng hoặc môi trường xung quanh ảnh hưởng xấu tới cuộc sống của Bên Thuê ở trong Căn hộ, thì Bên Thuê có thể chấm dứt Hợp đồng cho thuê nhà mà không bị phạt, tuy nhiên phải gửi cho Bên Cho Thuê thông báo trước 01 tháng. Bên Cho Thuê sẽ hoàn trả lại Bên Thuê đầy đủ toàn bộ số tiền đặt cọc và khoản tiền thuê nhà đã trả trước còn lại không sử dụng từ Bên Thuê.</p>
            <p>&nbsp;<span  ><strong>13.&nbsp;</strong><strong>TIẾNG ỒN TRONG CĂN HỘ</strong></span>&nbsp;</p>
            <p>Quy định về tiếng ồn trong căn hộ: mọi thiết bị âm thanh như: TV, đài phát thanh, thiết bị nghe phát nhạc mà có thể gây ra tiếng ồn phải được điều chỉnh âm thanh phù hợp để không gây ảnh hưởng đến những người thuê khác. Không tổ chức chơi nhạc theo nhóm trong căn hộ vào bất cứ thời gian nào.</p>
            <p><span  >&nbsp;<strong>14.&nbsp;</strong><strong>SỬ DỤNG KHU VỰC CHUNG</strong></span></p>
            <p>Trong trường hợp, Bên thuê có nhu cầu tổ chức tiệc ở sảnh tòa nhà, sân thượng hoặc sử dụng khu vực này vì mục đích cá nhân, Bên thuê phải hỏi ý kiến của Bên cho thuê trước và trả phí sử dụng như quy định. Phải thông báo trước cho Bên cho thuê. Để Bên cho thuê có thể thông báo cho các Bên thuê khác.&nbsp;</p>
            </div>
        </div>
      </div>
      </div>
            </div>
            <Footer />
        </>
    )
}

export default TermsContainer
